<template>
  <section id="contact">
    <h2 class="regular">Kontakt</h2>

    <form v-show="!sent" v-on:submit.stop.prevent="onSubmit">
      <div class="contact-container">
        <fieldset class="field-col-1">
          <input type="checkbox" name="is_user" autocomplete="off" tabindex="-1">
          <input placeholder="Ihr Name" name="name" type="text" pattern="^[A-Za-z0-9\s]{1,}$" required>
          <input placeholder="Ihre E-Mail" name="email" type="email" required>
          <input placeholder="Ihre Telefonnummer" name="phone" type="text" pattern="^\+?(?:[0-9]-?\/?){6,14}[0-9]$" required>
        </fieldset>
        <fieldset class="field-col-2">
          <textarea placeholder="Ihre Nachricht" name="message" required></textarea>
          <button class="button secondary send-message">
            <span>Nachricht senden</span>
            <i class="lni lni-telegram-original"></i>
          </button>
        </fieldset>
      </div>
    </form>

    <div v-show="sent">Vielen Dank fr Ihre Nachricht!</div>
  </section>
</template>

<script>
  export default {
    name: 'v-contact-form',
    data: function () {
      return {
        sent: false
      }
    },
    methods: {
      onSubmit: function (e) {
        this.ajax('/php/mail.php', {
          'name': e.target.querySelector('input[name="name"]').value,
          'email': e.target.querySelector('input[name="email"]').value,
          'phone': e.target.querySelector('input[name="phone"]').value,
          'message': e.target.querySelector('textarea[name="message"]').value,
          'is_user': e.target.querySelector('input[name="is_user"]').value,
        })
          .then(response => {
            console.log('before', this.sent);
            this.sent = true;
            console.log('after', this.sent);
          })
          .catch(e => {
            console.log(e);
          });
      },
      ajax: (url, params, requestHeader = [], responseType = 'json') => {
        return new Promise((resolve, reject) => {
          const xhr = new XMLHttpRequest();

          xhr.onreadystatechange = () => {
            if (xhr.readyState == 4 && xhr.status == 200) {
              if (responseType == 'json') {
                const response = JSON.parse(xhr.responseText);
                console.log('response', response);
                response.status ? resolve(JSON.parse(xhr.responseText)) : reject(JSON.parse(xhr.responseText));
              } else {
                resolve(xhr.responseText);
              }
            }
          };

          xhr.open('POST', url, true);
          xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
          requestHeader.map((v, k) => xhr.setRequestHeader(k, v));

          const paramString = Object.entries(params).reduce((paramArr, param) => {
            const paramName = param[0],
              paramValue = param[1];

            if (paramValue instanceof Array) {
              paramValue.map((value, key) => paramArr.push(paramName + `[${key}]=` + encodeURIComponent(value)));
            } else {
              paramArr.push(paramName + '=' + encodeURIComponent(param[1]));
            }

            return paramArr;
          }, []).join('&');

          xhr.send(paramString);
        });
      }
    },

  }
</script>

<style scoped lang="scss">
  @import '~@/variables';

  input[type="checkbox"] {
    display: none;
    visibility: hidden;
    opacity: 0;
  }

  .contact-container {
    display: flex;
    flex-direction: column;

    fieldset {
      flex: 1;
      border: 0;
      padding: 0;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
    }

    textarea {
      min-height: 8rem;
    }
  }

  @media (min-width: 768px) {
    .contact-container {
      flex-direction: row;

      .field-col-1 {
        margin-right: 0.5rem;
      }

      .field-col-2 {
        margin-left: 0.5rem;

      }

      textarea {
        flex: 1;
        min-height: auto;
      }
    }
  }
</style>