</script>

<style scoped lang="scss">
  @import '~@/variables';

  @keyframes slideInFromLeft {
    from {
      opacity: 0;
      transform: translateX(-80px);
    }

    to {
      opacity: 1;
      transform: translateX(0);
    }
  }

  @keyframes slideInFromRight {
    from {
      opacity: 0;
      transform: translateX(80px);
    }

    to {
      opacity: 1;
      transform: translateX(0);
    }
  }

  header {
    background: url('../assets/AdobeStock_408345976_Preview.jpeg');
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    min-height: 350px;
    display: flex;
    flex-direction: column;
    align-items: center;

    &>div:last-of-type {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }

  @media (min-width: 768px) {
    header {
      min-height: 500px;
    }
  }

  @media (min-width: 1000px) {
    header {
      min-height: 640px;
    }
  }

  .sticky-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    z-index: 10;
    padding: 1rem;
    background-color: #fff;
    top: 0;
    left: 0;
    width: 100%;
