      color: $ops-color-1;
    }

    i {
      margin-right: 0.25rem;
    }

    small {
      font-size: 0.7rem;
      line-height: 1.15;
    }
  }

  .upper-bar,
  .lower-bar {
    position: absolute;
    width: 100%;
    bottom: 0;

    div {
      position: absolute;
      width: calc(50% - 20px);
      height: 100%;
      display: block;
      content: '';
      flex: 1;

      &:after {
        content: "";
        position: absolute;
        top: 0;
        width: 0;
        height: 0;
        border-top-style: solid;
        border-top-color: transparent;
        border-bottom: 0 solid transparent;
      }

      &:first-of-type {
        left: 0;
        top: 0;

        &:after {
          left: 100%;
          border-left-style: solid;
        }
      }

      &:last-of-type {
        right: 0;
        top: 0;

        &:after {
          right: 100%;
          border-right-style: solid;
        }
      }
    }
  }

  .upper-bar {
    height: 25px;

    div {
      width: calc(50% - 17px);
      background-color: #ccc;

      &:after {
        border-top-width: 25px;
      }

      &:first-of-type {
        &:after {
          border-left-width: 25px;
          border-left-color: #ccc;
        }
      }

      &:last-of-type {
        right: 0;
        top: 0;

        &:after {
          right: 100%;
          border-right-width: 25px;
          border-right-color: #ccc;
        }
      }
    }
  }

  .lower-bar {
    height: 20px;

    div {
      width: calc(50% - 20px);
      background-color: #fff;

      &:after {
        border-top-width: 20px;
      }

      &:first-of-type {
        &:after {
          border-left-width: 20px;
          border-left-color: #fff;
        }
      }

      &:last-of-type {
        &:after {
          border-right-width: 20px;
          border-right-color: #fff;
        }
      }
    }
  }
</style>